import { createSlice } from "@reduxjs/toolkit";
import { getSetupInfo, getNewSetupName } from "./ui.helpers";

export const setupsSlice = createSlice({
  name: "ui",
  initialState: {
    tab: "Calculator",
    settingsOn: false,
    setups: [],
    stdSetupI: 0,
    cmpSetupIs: [0],
    importType: 0,
    importInfo: null,
    pendingImportCode: 0
  },
  reducers: {
    CHANGE_TAB: (state, action) => {
      state.tab = action.payload;
    },
    RESET_UI: (state) => {
      state.setups = [getSetupInfo({})];
      state.stdSetupI = 0;
      state.cmpSetupIs = [0];
      state.settingsOn = false;
    },
    CHANGE_STD_SETUP: (state, action) => {
      const newIndex = action.payload;
      const { cmpSetupIs } = state;
      if (cmpSetupIs.length === 1) {
        cmpSetupIs[0] = newIndex;
      } else {
        const i = cmpSetupIs.indexOf(newIndex);
        if (i === -1) {
          cmpSetupIs.unshift(newIndex);
        } else if (i > 0) {
          [cmpSetupIs[0], cmpSetupIs[i]] = [cmpSetupIs[i], cmpSetupIs[0]];
        }
      }
      state.stdSetupI = newIndex;
    },
    OPEN_SETTINGS: (state) => {
      state.settingsOn = true;
    },
    CLOSE_SETTINGS: (state) => {
      state.settingsOn = false;
    },
    APPLY_SETTINGS_UI: (state, action) => {
      const { setups, cmpSetupIs, standardI } = action.payload;
      state.setups = setups;
      state.cmpSetupIs = cmpSetupIs;
      state.stdSetupI = standardI;
      state.settingsOn = false;
    },
    START_IMPORT: (state, action) => {
      const { importInfo, type } = action.payload;
      state.importType = type;
      state.importInfo = importInfo;
    },

    PENDING_IMPORT: (state, action) => {
      state.pendingImportCode = action.payload;
    },
    END_IMPORT_ADD_SETUP: (state) => {
      const { ID, type } = state.importInfo;
      state.setups.push(
        getSetupInfo({ name: getNewSetupName(state.setups), ID, type })
      );
    },
    END_IMPORT_NEW_SESSION: (state) => {
      const { ID, type } = state.importInfo;
      state.setups = [getSetupInfo({ ID, type })];
      state.stdSetupI = 0;
      state.cmpSetupIs = [0];
    },
    TO_IMPORTED: (state) => {
      state.importInfo = null;
      state.importType = 0;
      state.pendingImportCode = 0;
      state.tab = "Calculator";
      state.settingsOn = false;
    },
    ABORT_IMPORT: (state) => {
      state.importInfo = null;
      state.importType = 0;
      state.pendingImportCode = 0;
    }
  }
});

export const {
  CHANGE_TAB,
  RESET_UI,
  CHANGE_STD_SETUP,
  OPEN_SETTINGS,
  CLOSE_SETTINGS,
  APPLY_SETTINGS_UI,
  START_IMPORT,
  PENDING_IMPORT,
  END_IMPORT_ADD_SETUP,
  END_IMPORT_NEW_SESSION,
  TO_IMPORTED,
  ABORT_IMPORT
} = setupsSlice.actions;

export default setupsSlice.reducer;
