import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  calculator,
  IMPORT_ADD_SETUP,
  IMPORT_NEW_SESSION
} from "../../redux/calculatorSlice";
import {
  END_IMPORT_ADD_SETUP,
  END_IMPORT_NEW_SESSION,
  PENDING_IMPORT,
  TO_IMPORTED
} from "../../redux/uiSlice";
import { ModalCarpet } from "../../styledCpns/Layout";
import { useImportedSetup } from "./helpers";
import { LoadingBar } from "./styles";

export default function Importing() {
  const { char, target } = useSelector(calculator);
  const { setups } = useSelector((state) => state.ui);
  const dispatch = useDispatch();
  const importedSetup = useImportedSetup();

  useEffect(() => {
    const execute = (fn) => setTimeout(fn, 100);
    if (char) {
      if (char.name === importedSetup.char.name) {
        if (setups.length === 4) {
          execute(() => {
            dispatch(PENDING_IMPORT(2));
          });
        } else {
          const sameChar = areEqual(char, importedSetup.char);
          const sameTg = areEqual(target, importedSetup.target);
          if (sameChar && sameTg) {
            execute(() => {
              dispatch(
                IMPORT_ADD_SETUP({
                  imported: importedSetup,
                  overwriteChar: false,
                  overwriteTg: false
                })
              );
              dispatch(END_IMPORT_ADD_SETUP());
              dispatch(TO_IMPORTED());
            });
          } else {
            let code = 3;
            if (!sameChar) code = code * 10;
            if (!sameTg) code = code * 10 + 1;
            execute(() => dispatch(PENDING_IMPORT(code)));
          }
        }
      } else {
        execute(() => {
          dispatch(PENDING_IMPORT(1));
        });
      }
    } else {
      execute(() => {
        dispatch(IMPORT_NEW_SESSION(importedSetup));
        dispatch(END_IMPORT_NEW_SESSION());
        dispatch(TO_IMPORTED());
      });
    }
  }, []);

  return (
    <ModalCarpet>
      <LoadingBar>
        <div />
      </LoadingBar>
    </ModalCarpet>
  );
}

function areEqual(obj1, obj2) {
  for (const k of Object.keys(obj1)) {
    if (obj1[k] !== obj2[k]) return false;
  }
  return true;
}
